import {Alert, Box, Button, Snackbar, useMediaQuery} from "@mui/material";
import {useRef, useState} from "react";
import {DateTime} from "luxon";
import {styled} from "@mui/system";
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import {Additional} from "../constant";

const Wrapper = styled(Box)({
  display: "flex",
  alignItems: "center",
  flexDirection: "column",
  maxWidth: "900px",
  padding: "0 25px",
  margin: "50px auto",
});

const ButtonsWrap = styled(Box)({
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  width: "100%",
});

const TextWrap = styled(Box)({
  textAlign: "start",
  marginTop: "50px",
  cursor: "pointer",
  ":hover": {
    backgroundColor: "lightgray"
  }
});
const OrderConfirmation = () => {
  const [setColor, setSetColor] = useState("");
  const [setType, setSetType] = useState("");
  const [sendDate, setSendDate] = useState<DateTime>();
  const [deliveryDate, setDeliveryDate] = useState<DateTime>();
  const [additional, setAdditional] = useState("");
  const [open, setOpen] = useState(false);
  const text = `  \n     "${setType}" ${setColor}  ${additional && ` + ${additional}`}.
  \n\n          ${sendDate &&
          sendDate.setLocale("ua").toFormat("dd MMMM")}(${sendDate && sendDate.toRelativeCalendar()}).\n  : ${deliveryDate && deliveryDate.setLocale("ua")
        .toFormat("dd MMMM")}(${deliveryDate && deliveryDate.toRelativeCalendar()}),    ? `;

  const isMobile = useMediaQuery('(max-width:1023px)');
  const handleColorChange = (color: string) => {
    setSetColor(color);
  }
  const handleTypeChange = (type: string) => {
    setSetType(type);
  }

  const handleDateChange = (type: string, date: DateTime) => {
    type === "send" ? setSendDate(date) : setDeliveryDate(date);
  }

  const handleAdditional = (add) => {
    setAdditional(add);
  }

  const copyToClipboard = () => {
    navigator.clipboard.writeText(text);
    setOpen(true);
  };

  const handleClose = (event?: React.SyntheticEvent | Event, reason?: string) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };

  return (
    <Wrapper>
      <ButtonsWrap style={{flexDirection: isMobile ? "column" : "row"}}>
        <Box>:</Box>
        <Box style={{flexDirection: isMobile ? "column" : "row", display: "flex"}}>
          <Button onClick={() => handleTypeChange("")}></Button>
          <Button onClick={() => handleTypeChange("")}></Button>
          <Button onClick={() => handleTypeChange("")}></Button>
        </Box>
      </ButtonsWrap>
      <ButtonsWrap style={{flexDirection: isMobile ? "column" : "row"}}>
        <Box>:</Box>
        <Box style={{flexDirection: isMobile ? "column" : "row", display: "flex"}}>
          <Button onClick={() => handleColorChange("")}></Button>
          <Button onClick={() => handleColorChange("")}></Button>
          <Button onClick={() => handleColorChange("")}></Button>
        </Box>
      </ButtonsWrap>
      <ButtonsWrap style={{flexDirection: isMobile ? "column" : "row"}}>
        <Box> :</Box>
        <Box style={{flexDirection: isMobile ? "column" : "row", display: "flex"}}>
          <Button onClick={() => handleDateChange("send", DateTime.now())}></Button>
          <Button onClick={() => handleDateChange("send", DateTime.now().plus({day: 1}))}></Button>
          <Button onClick={() => handleDateChange("send", DateTime.now().plus({day: 2}))}></Button>
        </Box>
      </ButtonsWrap>
      <ButtonsWrap style={{flexDirection: isMobile ? "column" : "row"}}>
        <Box> :</Box>
        <Box style={{flexDirection: isMobile ? "column" : "row", display: "flex"}}>
          <Button onClick={() => handleDateChange("deliver", DateTime.now())}></Button>
          <Button onClick={() => handleDateChange("deliver", DateTime.now().plus({day: 1}))}></Button>
          <Button onClick={() => handleDateChange("deliver", DateTime.now().plus({day: 2}))}></Button>
        </Box>
      </ButtonsWrap>
      <ButtonsWrap style={{flexDirection: isMobile ? "column" : "row"}}>
        <Box>:</Box>
        <Box style={{flexDirection: isMobile ? "column" : "row", display: "flex"}}>
          <Button onClick={() => handleAdditional(Additional.carts)}>+ </Button>
          <Button onClick={() => handleAdditional(Additional.stand)}>+ </Button>
          <Button onClick={() => handleAdditional(Additional.cartsAndStand)}>+   </Button>
        </Box>
      </ButtonsWrap>
      <TextWrap onClick={copyToClipboard} maxWidth="500px">
          <br />
             &quot;{setType}&quot; {setColor} 
        {additional && ` + ${additional}`}.<br />
        <br />
                  {sendDate &&
          sendDate.setLocale("ua").toFormat("dd MMMM")}({sendDate && sendDate.toRelativeCalendar()}).<br />
          : {deliveryDate && deliveryDate.setLocale("ua")
        .toFormat("dd MMMM")}({deliveryDate && deliveryDate.toRelativeCalendar()}),    ? 
      </TextWrap>
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
          Copied!
        </Alert>
      </Snackbar>
    </Wrapper>
  )
};

export default OrderConfirmation;
